.block-buttons {
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
}

.form-edit-stavka {
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    z-index: 6;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 200px;
    margin: auto;
}

.edit-send {
    width: 370px;
    height: 166px;
    border: none;
    background-size: cover;
    background-color: transparent;
    color: #ffffff;
    font-size: 15px;
    cursor: pointer;
    /* margin-bottom: 15px; */
}

.button-send {
    width: 220px;
    height: 38px;
    border: none;
    background-size: cover;
    background-color: transparent;
    color: #ffffff;
    font-size: 15px;
    cursor: pointer;
    margin-bottom: 15px;
}


input {
    width: 270px;
    margin: 0 0;
    border: none;
    height: 38px;
    background-color: transparent;
    margin-top: 70px;
    padding: 0 10px;
    color: #fff;
    font-size: 16px;
}