.block-buttons {
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
}

.form-wraper {
    margin-top: 150px;
    position: relative;
    z-index: 6;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-new-worker1 {
    width: 100%;
    height: 172px;
    border-radius: 10px;
    background-color: #000000;
    color: #ffffff;
    font-size: 15px;
    cursor: pointer;
    position: relative;
    z-index: 5;
    margin: auto;
    text-align: center;
}

input::placeholder {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    /* identical to box height */
    letter-spacing: -0.045em;

    /* ULEY Светлый */
    color: #E8F1F9;

    opacity: 0.4;
}

