.mySelect {
    position: relative;
    min-width: 100%;
    -webkit-appearance: none;
    padding: 17px 40px 17px 12px;
    border: 2px solid #2e7cff;
    border-radius: 10px;
    background: #2A2731;
    box-shadow: 0 1px 3px -2px #9098A9;
    cursor: pointer;
    font-family: inherit;
    font-size: 16px;
    transition: all 150ms ease;
    color: #fff;
}